import { FC, useEffect, useState } from 'react';
import ReactApexChart from 'react-apexcharts';
import { ApexOptions } from 'apexcharts';
import { client } from '../../../App';
import { GET_QUERY } from '../../../queries/history';
import moment from 'moment';
import { GET_AUTOMATED_BAR_CHART, GET_AUTOMATED_COLUMNS } from '../../../queries/widgets';
import { ITheme } from './../widgetBuilder';
import { useMediaQuery } from '@mui/material';

type Props = {
  color: string;
  content: {
    title?: string | { isDynamic: boolean };
    chartData: {
      name: string;
      data: number[];
    }[];
    categories: string[];
    isPercent: boolean;
    chartType: string;
    isin: string;
    color: string;
    automationType?: string;
    legend?: Record<string, string>;
  };
  theme?: ITheme;
};

export const BarChart: FC<Props> = ({ content, color, theme }) => {
  const [periodsData, setPeriodsData] = useState<any[]>([[]]);
  const [periodLabels, setPeriodLabels] = useState<string[]>();
  const [dynamicTitle, setDynamicTitle] = useState('');
  const isMobile = useMediaQuery('(max-width:600px)');

  const series = content?.isPercent
    ? content?.chartData.map((item) => {
        const firstValue = item.data[0];
        return {
          name: item.name,
          data: item.data.map((bar) => (bar / firstValue - 1) * 100),
        };
      }) || []
    : content?.chartData || [];
  const categories = content?.categories || [];
  console.log(series, '===', periodsData);

  const fetchData = async (mode: string) =>
    client.query({
      query: GET_QUERY,
      variables: { param: { type: 'Axxion', isin: content?.isin, mode } },
    });

  const fetchBarChart = async () =>
    client.query({
      query: GET_AUTOMATED_BAR_CHART,
      variables: { isin: content?.isin, type: content?.automationType },
    });

  const fetchAutomatedColumns = async (columns: any[]) =>
    client.query({
      query: GET_AUTOMATED_COLUMNS,
      variables: { param: { data: columns, isin: content?.isin } },
    });

  useEffect(() => {
    if (content?.title && typeof content.title === 'object' && content.title.isDynamic) {
      fetchAutomatedColumns([content?.title]).then((res) => {
        setDynamicTitle(res.data?.getAutomatedColumns?.[0]?.[0]);
      });
    }
  }, [content?.title]);

  useEffect(() => {
    if (content?.chartType === 'Axxion' && content?.isin) {
      fetchData('all').then((result) => {
        const data = result.data.getQuery.histories;
        let year = 1;
        let totalForYears = 0;
        let total = 0;
        let count = 0;
        const chartValues = [];
        const periods = [];
        const firstValue = data[0][1];
        for (let i = data.length - 1; i >= 0; i--) {
          const item = data[i];
          const [date, value] = item;
          total += value;
          const lastDate = moment(data[data.length - 1][0]).subtract(year, 'year');
          if (moment(date).isBefore(lastDate)) {
            chartValues.push((totalForYears / count / firstValue - 1) * 100);
            year++;
            totalForYears = 0;
            count = 0;
            periods.push(`${lastDate.format('DD.MM.YYYY')} bis ${lastDate.add(1, 'year').format('DD.MM.YYYY')}`);
          } else {
            totalForYears += value;
            count++;
          }
        }
        chartValues.push((total / data.length / firstValue - 1) * 100);
        periods.push('Tag der Anlage');
        setPeriodsData([{ name: '', data: chartValues.reverse() }]);
        setPeriodLabels(periods.reverse());
      });
    }

    if (content?.chartType === 'Automated') {
      fetchBarChart().then((result) => {
        const data = result.data.getAutomatedBarChart;
        
        if (content?.automationType === 'all') {
          // Group data by type
          const groupedData = data.reduce((acc: any, item: any) => {
            if (!acc[item.type]) {
              acc[item.type] = [];
            }
            acc[item.type].push(item);
            return acc;
          }, {});

          const mainColors = [theme?.colorSet.primary, theme?.colorSet.secondary];

          const chartSeries = Object.keys(groupedData).map((type, index) => ({
            name: (content?.legend && content.legend[type])
              ? content.legend[type]
              : type.replace(/_/g, ' ').replace(/\b\w/g, (l) => l.toUpperCase()),
            data: groupedData[type].map((item: any) => item.value),
            color: Object.keys(groupedData).length === 2
              ? mainColors[index]
              : theme?.colorSet.gradientColors[index],
          }));

          const allPeriods: string[] = data.map((item: any) => {
            const toDate = moment(item.end, 'DD.MM.YYYY');
            const fromDate = moment(item.end, 'DD.MM.YYYY').subtract(1, 'year');
            const day = fromDate.day();
            if (day === 0) fromDate.subtract(2, 'day');
            else if (day === 6) fromDate.subtract(1, 'day');
            return `${fromDate.format('DD.MM.YYYY')} bis ${toDate.format('DD.MM.YYYY')}`;
          });

          // Remove duplicates and set labels
          const uniquePeriods = Array.from(new Set(allPeriods));
          setPeriodLabels(uniquePeriods);
          setPeriodsData(chartSeries);
        } else {
          setPeriodLabels(
            data.map((item: any) => {
              const toDate = moment(item.end, 'DD.MM.YYYY');
              const fromDate = moment(item.end, 'DD.MM.YYYY').subtract(1, 'year');
              const day = fromDate.day();
              if (day === 0) fromDate.subtract(2, 'day');
              else if (day === 6) fromDate.subtract(1, 'day');
              return `${fromDate.format('DD.MM.YYYY')} bis ${toDate.format('DD.MM.YYYY')}`;
            })
          );
          setPeriodsData([{ name: '', data: data.map((item: any) => item.value) }]);  
        }
      });
    }
  }, [content?.chartType]);

  const options: ApexOptions = {
    chart: {
      type: 'bar',
      height: 350,
    },
    legend: {
      show: content?.automationType === 'all',
      showForSingleSeries: true,
      showForNullSeries: true,
      showForZeroSeries: true,
      position: 'bottom',
      itemMargin: {
        vertical: 16        
      },
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: isMobile ? '90%' : '50%',
        dataLabels: {
          hideOverflowingLabels: true,
          position: 'center',
        },
      },
    },
    dataLabels: {
      enabled: true,
      formatter: function (val) {
        const value = typeof val === 'number' ? val : Number(val);
        return `${value.toFixed(2)}${(content?.isPercent || content?.chartType === 'Automated') ? '%' : ''}`;
      },
      offsetY: 0,
      style: {
        fontSize: isMobile ? '8px' : '12px',
        fontWeight: 700,
        colors: [color === '#ccd5df' ? '#fff' : '#fff'],
      },
    },
    stroke: {
      show: true,
      width: 0,
      colors: ['#fe0f31'],
    },
    tooltip: {
      enabled: true,
      followCursor: true,
      custom: function ({ series, seriesIndex, dataPointIndex }) {
        return isMobile && content?.chartType === 'Automated' ? `
          <div class="custom-tooltip" style="border-color: ${color}; text-align: center;">
            ${periodLabels?.[seriesIndex]}
            <br />
            <b>${series[seriesIndex][dataPointIndex]?.toFixed(2)}%</b>
          </div>
        ` : `
          <div class="custom-tooltip" style="border-color: ${color};">
            ${series[seriesIndex][dataPointIndex]?.toFixed(2)}%
          </div>
        `;
      },
    },
    states: {
      normal: {
        filter: { type: 'none', value: 0 },
      },
      hover: {
        filter: { type: 'none', value: 0 },
      },
      active: {
        filter: { type: 'none', value: 0 },
      },
    },
    xaxis: {
      categories: content?.chartType !== 'Manuall' ? periodLabels : categories,
      labels: {
        formatter: function (val) {
          if (!isMobile || typeof val !== 'string') return val;
          const parts = val.split(' bis ');
          if (parts.length !== 2) return val;
          const from = moment(parts[0], 'DD.MM.YYYY', true);
          const to = moment(parts[1], 'DD.MM.YYYY', true);
          if (from.isValid() && to.isValid()) return `${from.format('YYYY')}/${to.format('YYYY')}`;
          return val;
        },
      },
    },
    yaxis: {
      title: {
        text: isMobile ? '' : 'in %',
        style: {
          fontWeight: 500,
        },
      },
      labels: {
        formatter: function (val) {
          return `${Math.floor(val)}${content?.isPercent ? '%' : ''}`;
        },
      },
    },
    fill: {
      opacity: 1,
    },
    colors: theme
      ? periodsData.length === 2
      ? [theme.colorSet.primary || color, theme.colorSet.secondary || color]
      : theme.colorSet.gradientColors
      : [content.color || color],
  };

  return (
    <>
      {(typeof content?.title === 'string' || (typeof content?.title === 'object' && content?.title?.isDynamic && dynamicTitle)) && (
        <div className="widget-title" style={{ color: color === '#ccd5df' ? '#003F7A' : color }}>
          {typeof content.title === 'object' && content.title.isDynamic 
            ? dynamicTitle 
            : typeof content.title === 'string' 
              ? content.title 
              : ''}
        </div>
      )}
      <ReactApexChart
        type="bar"
        height={350}
        options={options}
        series={content?.chartType !== 'Manuall' ? periodsData : series}
      />
    </>
  );
};
